
.header-slider{
    width: 100%;
    height: 100vh;
    // z-index: 100; 
    position: relative;
	&__heading{
		text-align: center;
        text-transform: uppercase;
        @include posA($top: 50%, $left: 50%, $delta_x: -50%, $delta_y: -50%);
        width: 100%;
        z-index: 1;
	}
	&__title{
        @include font($title-ff, 7rem, $body-color, $fw: 700, $lh: 1.6);
        letter-spacing: .05em;
        z-index: 10;
        margin: 0;
	}
	&__subtitle{
        @include font($title-ff, 1.6rem, $body-color, $fw: 400, $lh: 1.6);
        letter-spacing: .3em;
       
    }
    & .slick-dots {
        @include posA($top: 35%, $left: null, $delta_x: null, $delta_y: null);
		right: 4%;
	}
}

.slick-dots {
    list-style: none;
    margin: 0;
    padding: 0;
    & button {
        display: none;      
    }
    & li {
    width: 9px;
    height: 9px;
    border: $border-width solid $body-color;
    border-radius: 50%;
    background-color: transparent;
    margin-bottom: 40px;
    cursor: pointer;
    @include tr (all);
        @include hover {
        background-color: $body-color;
        transform: scale(1.4)
        }
    }
}


.news-slider {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;

	& .slick-dots {
		padding: 0;
    	// margin: ;
		@include posA ($top: auto, $left: 50%, $delta_y: 0); 
		bottom: -9%;

		& li {
			margin: 0 30px ;
			display: inline-block;
		}
	}
}

.slide-news{
	padding: 0 5px;
}

.slider__arrow {
	position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    display: none !important;
    z-index: 10;
	cursor: pointer;
	
}

@media screen and (min-width: 768px) {
    .slider__arrow {
        display: block !important;
    }
}

.slider__arrow--prev {
    left: -7%;
}

.slider__arrow--next {
    right: -7%;
}

.slider__arrow--prev i {
    display: inline-block;
    margin-left: -3px;
}

.slider__arrow--next i {
    display: inline-block;
    margin-right: -3px;
}